#main-banner
	.box-image
		.content
			@apply text-white text-center
	.title
		color: #FFFFFF
		font-size: r(16px)
		@apply font-normal
		@media (min-width: 576px)
			font-size: r(20px)
		@media (min-width: 1280px)
			font-size: r(32px)
			line-height: r(35px)

	.line
		height: r(1px)
		width: r(40px)
		@apply bg-primary-F6E18D mx-auto xl:mb-5 mb-15px
		@media (min-width: 576px)
			width: r(60px)

	.ctn
		text-align: center
		font-size: r(20px)
		@apply font-bold xl:mt-5
		@media (min-width: 1280px)
			font-size: r(30px)
		@media (min-width: 1280px)
			font-size: r(56px)
			line-height: r(62px)

	.container
		left: 50%
		transform: translateX(-50%)
		bottom: r(35px)
		@apply w-full absolute z-5
		@media (min-width: 1280px)
			bottom: r(115px)
	.swiper-pagination
		bottom: r(10px) !important
		@media (min-width: 1280px)
			bottom: r(40px) !important
		span
			width: r(10px)
			height: r(10px)
			border: r(1px) solid #FFFFFF
			@apply rounded-none transition-all
			&.swiper-pagination-bullet-active
				width: r(20px)
				@apply bg-white
