.button
	transition: .3s all ease-in-out
	user-select: none
	*
		@media (min-width: 576px)
			white-space: nowrap

	.btn
		user-select: none
		cursor: pointer
		width: fit-content
		transition: .3s all ease-in-out
		position: relative
		display: flex
		justify-content: center
		align-items: center
		padding: r(7px) r(19.5px)
		border-radius: r(90px)
		border: r(1px) solid transparent
		@apply text-white text-lg leading-5 transition-all
		&.btn-1
			@apply font-bold border-primary-BB9C66 text-white
			&:hover
				@apply bg-primary-BB9C66
		&.btn-2
			@apply bg-primary-BB9C66 font-bold
			&:hover
				@apply bg-primary-A89142
		&.btn-view-more
			span
				@apply font-bold uppercase text-primary-051A3A text-lg
			em
				font-size: 16px
				height: 16px
				line-height: 16px
				margin-left: 6px
				@apply text-primary-BB9C66
			&.white
				span,em
					@apply text-white
		&.btn-3
			padding: r(9px) r(24px)
			border-width: r(1px)
			@apply text-primary-051A3A border-primary-051A3A font-bold uppercase
			&::after
				content: "\f178"
				@apply font-Awesome6 text-xl leading-5 ml-10px font-light
			&:hover
				@apply bg-primary-BB9C66 text-white border-primary-BB9C66
			&.btn-border-white
				@apply border-white text-white
			&.btn-border-yellow
				@apply border-primary-F6E18D text-primary-F6E18D
		&.has-input-submit
			@apply relative
			span.wpcf7-spinner
				left: calc( 100% + 30/1920*100rem )
				@apply absolute left-full top-1/2 -translate-y-1/2
			&::before
				content: attr(data-title)
				@apply normal-case
			input
				font-size: 0
				@apply absolute top-0 left-0 w-full h-full
.button-wrapper
	@apply flex gap-10px
