.block-title
	font-size: r(34px)
	@apply font-bold uppercase text-primary-051A3A text-center flex flex-col
	@media (min-width: 1280px)
		font-size: r(40px)
		line-height: r(48px)
	span
		font-size: r(30px)
		@apply font-light
		@media (min-width: 1280px)
			font-size: r(36px)
			line-height: r(40px)

.medium-title
	font-size: r(30px)
	@apply font-bold text-primary-051A3A
	@media (min-width: 1280px)
		font-size: r(36px)
		line-height: r(44px)
